import React,{ Suspense, lazy }  from 'react';
import ReactDOM from 'react-dom';
import SearchAppBar from './components/Header'
import { BrowserRouter as Router, Route,Switch } from "react-router-dom";
import 'react-toastify/dist/ReactToastify.css';
import '../src/style.css';

export default class App extends React.Component{
    render(){
        const RouteData = lazy(() => import('./components/Router'));
        const TechnoDetails = lazy(() => import('./components/QuesionList'));
        const UserComments = lazy(() => import('./components/UserComment'));
        return(
            <div>
                <Router>
                <SearchAppBar/>
                <Suspense fallback={<div>Loading...</div>}>
                <Switch>
                  <Route path="/" exact component={RouteData} />
                  <Route  path="/Quesionlist/:id" component={TechnoDetails} />
                  <Route  path="/UserComments/:id" component={UserComments} />
                  </Switch>
                  </Suspense>
                </Router>
            </div>
        )
    }
}

ReactDOM.render(<App />, document.getElementById('root'));


